import React, { useState, useEffect } from 'react';
import {
  ItemWrapper,
  Item,
  TitleImg,
  MovieInfo,
  PhotoContainer,
  TitleItem,
  InfoContainer,
  GoBack,
  Sec,
  SeeMore,
  Posters,
  PosterWrapper,
  CenterTitle,
  PhotoGallery,
  OutWrp,
  CloseButton,
  OpenFull,
  VideoWrapper,
  ResponsiveVideoOuter,
  ResponsiveVideoWrapper,
  MovieInfo2,
} from '../styles/searchstyles';
import Grid from '@material-ui/core/Grid';
import EmptyPoster from '../../img/empty_poster.jpg';
import EmptyActor from '../../img/empty_actor.jpg';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import CloseIcon from '@material-ui/icons/Close';

function Detailsmovie(props) {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [isLoaded2, setIsLoaded2] = useState(false);
  const [isLoaded3, setIsLoaded3] = useState(false);
  const [isLoaded4, setIsLoaded4] = useState(false);
  const [item, setItem] = useState('');
  const [credits, setCredits] = useState('');
  const [actorsCount, setActorsCount] = useState(18);
  const [videos, setVideos] = useState('');
  const [art1, setArt1] = useState(6);
  const [art2, setArt2] = useState(6);
  const [vid, setVid] = useState(2);
  const [galery, setGalery] = useState(false);
  const [modalImg, setModalImg] = useState(false);
  const [Iz, setIz] = useState('');
  const api_key = '58666352553834b5820ef436680fb2e5';
  const tv_id = props.match.params.id;
  const havePhoto = ({ media_type, poster_path, profile_path, name, title }, size) => {
    if (media_type === 'movie' || media_type === 'tv') {
      if (poster_path) {
        if (size === 'big') {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w500/${poster_path}`} />;
        } else {
          return <TitleImg alt={title} src={`https://image.tmdb.org/t/p/w185/${poster_path}`} />;
        }
      } else {
        return <img alt={title} src={EmptyPoster} />;
      }
    } else {
      if (profile_path) {
        if (size === 'big') {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/h632/${profile_path}`} />;
        } else {
          return <TitleImg alt={name} src={`https://image.tmdb.org/t/p/w185/${profile_path}`} />;
        }
      } else {
        return <img alt={name} src={EmptyActor} />;
      }
    }
  };
  const handleMoreVideos = () => {
    if (vid === 1000) setVid(2);
    else setVid(1000);
  };
  const handleMoreImg = (a) => {
    switch (a) {
      case 1:
        if (art1 === 1000) setArt1(6);
        else setArt1(1000);
        break;
      case 2:
        if (art2 === 1000) setArt2(6);
        else setArt2(1000);
        break;
      default:
        break;
    }
  };
  const handleMore = () => {
    if (actorsCount === 1000) setActorsCount(18);
    else setActorsCount(1000);
  };
  const handleOpen = (src) => {
    setModalImg(src);
    setGalery(true);
  };
  const handleClose = () => {
    setGalery(false);
  };
  useEffect(() => {
    fetch(`https://api.themoviedb.org/3/tv/${tv_id}?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          result.media_type = 'tv';
          setItem(result);
          setIsLoaded(true);
          console.log(result);
          getFanarts(result.id);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/${tv_id}/credits?api_key=${api_key}&language=pl-PL`)
      .then((res) => res.json())
      .then(
        (result) => {
          setCredits(result);
          setIsLoaded2(true);
        },
        (error) => {
          setIsLoaded2(true);
          setError(error);
        }
      );
    fetch(`https://api.themoviedb.org/3/tv/${tv_id}/videos?api_key=${api_key}`)
      .then((res) => res.json())
      .then(
        (result) => {
          setVideos(result);
          console.log(result);
          setIsLoaded4(true);
        },
        (error) => {
          setIsLoaded4(true);
          setError(error);
        }
      );
    const getFanarts = (id) => {
      fetch(`https://api.themoviedb.org/3/tv/${id}/images?api_key=${api_key}`)
        .then((res) => res.json())
        .then(
          (result) => {
            setIz(result);
            setIsLoaded3(true);
          },
          (error) => {
            setIsLoaded3(true);
            setError(error);
          }
        );
    };
    window.scrollTo(0, 0);
  }, []);

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded || !isLoaded2 || !isLoaded3) {
    return <div style={{ minHeight: '100vh' }}></div>;
  } else {
    return (
      <>
        <PhotoGallery maxWidth='xs' fullWidth={true} open={galery} onClose={handleClose}>
          <img alt='Modal' src={modalImg} style={{ maxWidth: '100%', maxHeight: '100%' }} />
          <CloseButton onClick={handleClose}>
            <CloseIcon fontSize='large' />
          </CloseButton>
          <OpenFull target='_blank' href={modalImg}>
            Otwórz w pełnej rozdzielczości
          </OpenFull>
        </PhotoGallery>
        <Grid container spacing={0} style={{ postition: 'relative' }}>
          <GoBack>
            <ArrowBackIcon fontSize='large' onClick={() => props.history.goBack()} />
          </GoBack>
          <PhotoContainer item xs={12} md={6} lg={4}>
            {havePhoto(item, 'big')}
            <TitleItem>{item.name}</TitleItem>
            {item.first_air_date && ` ( ${item.first_air_date.slice(0, 4)} )`}
          </PhotoContainer>
          <InfoContainer item xs={12} md={6} xl={8}>
            <MovieInfo style={{ marginTop: '30px' }}>
              Opis: <br />
              <span>{item.overview ? item.overview : 'Brak polskiego opisu'}</span>
            </MovieInfo>
            {item.tagline && (
              <MovieInfo>
                Slogan: <span>{item.tagline}</span>
              </MovieInfo>
            )}
            <div style={{ borderTop: '1px solid gray', width: '100%', marginBottom: '5px' }} />
            <div style={{ display: 'flex', flexWrap: 'wrap' }}>
              <MovieInfo>
                Typ: <span>{item.media_type}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Pierwszy odcinek: <span>{item.first_air_date}</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Ostatni odcinek: <span>{item.last_air_date}</span>
              </MovieInfo>
              <MovieInfo>
                Status <span>{item.status}</span>
              </MovieInfo>
              <MovieInfo>
                Czas trwania: <span>{item.episode_run_time[0]} minut</span>
              </MovieInfo>{' '}
              <MovieInfo>
                Język: <span>{item.languages.map((i) => i).join(', ')}</span>
              </MovieInfo>
              <MovieInfo>
                {item.origin_country.length > 1 ? 'Kraje: ' : 'Kraj: '}
                <span>{item.origin_country.map((i) => i).join(', ')}</span>
              </MovieInfo>
              <MovieInfo>
                {item.production_companies.length > 1 ? 'Wytwórnie: ' : 'Wytwórnia: '}
                <span>{item.production_companies.map((i) => i.name).join(', ')}</span>
              </MovieInfo>
              <MovieInfo style={{ display: 'flex', alignItems: 'center' }}>
                Stacje:{' '}
                <span
                  style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'center', marginTop: '5px', marginLeft: '15px' }}>
                  {item.networks.map((i, index) => (
                    <img key={index} alt={i.name} style={{ margin: '0px 8px' }} src={`https://image.tmdb.org/t/p/w92/${i.logo_path}`} />
                  ))}
                </span>
              </MovieInfo>
            </div>
            {isLoaded4 && (
              <MovieInfo2 style={{ width: '100%' }}>
                {videos.results.length > 0 && (
                  <div
                    style={{
                      display: 'flex',
                      flexDirection: 'column',
                      alignItems: 'flex-start',
                      width: '100%',
                    }}>
                    {videos.results.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 0 && (
                      <div style={{ margin: '20px 15px 10px 15px', fontSize: '30px' }}>Zwiatun:</div>
                    )}
                    <VideoWrapper>
                      {videos.results
                        .filter((item) => item.type === 'Trailer' && item.site === 'YouTube')
                        .slice(0, vid)
                        .map((video, index) => (
                          <ResponsiveVideoOuter min={(vid > 2).toString()} key={index}>
                            <ResponsiveVideoWrapper>
                              <iframe
                                style={{ position: 'absolute', top: '0', left: '0', right: '0', bottom: '0', width: '100%', height: '100%' }}
                                allowFullScreen='allowfullscreen'
                                mozallowfullscreen='mozallowfullscreen'
                                msallowfullscreen='msallowfullscreen'
                                oallowfullscreen='oallowfullscreen'
                                webkitallowfullscreen='webkitallowfullscreen'
                                title={video.name}
                                src={`https://www.youtube.com/embed/${video.key}`}></iframe>
                            </ResponsiveVideoWrapper>
                          </ResponsiveVideoOuter>
                        ))}
                    </VideoWrapper>
                  </div>
                )}
                {videos.results.filter((item) => item.type === 'Trailer' && item.site === 'YouTube').length > 2 && (
                  <Grid item xs={12} onClick={() => handleMoreVideos()}>
                    {vid > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
                  </Grid>
                )}
              </MovieInfo2>
            )}
          </InfoContainer>
        </Grid>
        <Sec>Sezony</Sec>
        <ItemWrapper>
          {item.seasons.map((i, index) => {
            i.media_type = 'tv';
            return (
              <Item key={index} to={`/details/tv/${item.id}/season/${i.season_number}`}>
                {havePhoto(i)}
                <div className='tit2'>{i.name}</div>
              </Item>
            );
          })}
        </ItemWrapper>
        <Sec>Obsada</Sec>
        <ItemWrapper>
          {credits.cast.slice(0, actorsCount).map((person, index) => {
            return (
              <Item to={`/details/person/${person.id}`} key={index}>
                {havePhoto(person)}
                <div className='tit2'>
                  {person.name}
                  <br />
                  <span className='character'>{person.character}</span>
                </div>
              </Item>
            );
          })}
        </ItemWrapper>
        {credits.cast.length > 18 && (
          <div onClick={handleMore}>{actorsCount > 20 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}</div>
        )}
        <Sec style={{ marginBottom: '30px', marginTop: '120px' }}>Grafiki:</Sec>
        {Iz && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Tła
            </CenterTitle>
            {Iz.backdrops.slice(0, art1).map((im, index) => {
              return (
                <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                  <Posters
                    onClick={() => handleOpen(`https://image.tmdb.org/t/p/original/${im.file_path}`)}
                    src={`https://image.tmdb.org/t/p/w300/${im.file_path}`}
                    alt={im.id}
                  />
                </PosterWrapper>
              );
            })}
            {Iz.backdrops.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(1)}>
                {art1 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}{' '}
          </OutWrp>
        )}
        {Iz && (
          <OutWrp container spacing={0}>
            <CenterTitle item xs={12}>
              Plakaty
            </CenterTitle>
            {Iz.posters.slice(0, art2).map((im, index) => {
              return (
                <PosterWrapper item xs={12} md={6} lg={2} key={index}>
                  <Posters
                    onClick={() => handleOpen(`https://image.tmdb.org/t/p/original/${im.file_path}`)}
                    src={`https://image.tmdb.org/t/p/w300/${im.file_path}`}
                    alt={im.id}
                  />
                </PosterWrapper>
              );
            })}
            {Iz.posters.length > 6 && (
              <Grid item xs={12} onClick={() => handleMoreImg(2)}>
                {art2 > 6 ? <SeeMore>Pokaż mniej</SeeMore> : <SeeMore>Pokaż więcej</SeeMore>}
              </Grid>
            )}
          </OutWrp>
        )}
      </>
    );
  }
}

export default Detailsmovie;
